.favorites-bg {
  margin-bottom: 64px;
}

.favorites-icon {
  margin: auto auto 24px;
  display: block;
}

.favorites-error-text {
  max-width: 335px;
  text-align: center;
  color: var(--dark-text);
  margin: auto;
  font-family: Inter;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
}

.favorites-btn {
  min-width: 67px;
  height: 46px;
  border: 1px solid var(--input-color-theme-light);
  color: var(--input-color-theme-light);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 15px;
  margin-bottom: 24px;
  padding: 14px 18px;
  font-family: Inter;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
  transition: background-color .25s linear, color .25s linear, border .25s linear;
  display: block;
}

.favorites-btn:hover, .favorites-btn:focus {
  background-color: var(--accent-color);
  color: var(--theme-light);
  border: rgba(0, 0, 0, 0);
}

@media screen and (min-width: 768px) {
  .favorites-section {
    padding-bottom: 100px;
  }

  .favorites-icon {
    margin-bottom: 32px;
  }

  .favorites-error-text {
    max-width: 481px;
    font-size: 16px;
  }
}

@media screen and (min-width: 1280px) {
  .favorites-section {
    padding-top: 40px;
  }
}

